import React, { useState } from "react";
import Swiper from "swiper";
import "swiper/css";
// import "swiper/css/swiper.css";

const ImageGallery = ({ images, imgIdx, popFlag, setPopFlag }) => {
  const swiperRef = React.useRef(null);
  const handleSlideNext = () => {
    swiperRef.current.slideNext(); //    
  };
  const handleSlidePrev = () => {
    swiperRef.current.slidePrev(); //    
  };

  React.useEffect(() => {
    console.log("imgIdx", imgIdx);
    swiperRef.current = new Swiper(".swiper-container", {
      slidesPerView: 1, //     
      // loop: true, //      
      pagination: {
        el: ".swiper-pagination",
        clickable: true,
      },
      initialSlide: imgIdx,
    });
  }, [popFlag, imgIdx]);

  // if (!popFlag) {
  //   return <></>;
  // }

  return (
    <div className="popSlide">
      <div className="slideList">
        <div className="swiper-container">
          <div className="swiper-wrapper">
            {images.map((image, index) => (
              <div
                className="swiper-slide"
                key={index}
                style={{ width: "333px !important" }}
              >
                <div className="slide">
                  <img src={image.src} alt={`Image ${index}`} />
                </div>
              </div>
            ))}
          </div>
          <div className="swiper-pagination"></div>
          <div className="swiper-button-next" onClick={handleSlideNext}></div>
          <div className="swiper-button-prev" onClick={handleSlidePrev}></div>
        </div>
        <button
          type="button"
          className="closeSlide"
          onClick={() => setPopFlag(false)}
        >
           
        </button>
      </div>
    </div>
  );
};

export default ImageGallery;
