import React, { useState, useEffect } from "react";

function Timer() {
  const [timeLeft, setTimeLeft] = useState("");
  const [day, setDay] = useState("");
  const [hour, setHour] = useState("");
  const [minute, setMinute] = useState("");
  const [second, setSecond] = useState("");

  useEffect(() => {
    const countdownDate = new Date("2023-05-06T12:00:00").getTime();

    const calculateTimeLeft = () => {
      const now = new Date().getTime();
      const difference = countdownDate - now;

      if (difference > 0) {
        const days = Math.floor(difference / (1000 * 60 * 60 * 24));
        const hours = Math.floor(
          (difference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
        );
        const minutes = Math.floor(
          (difference % (1000 * 60 * 60)) / (1000 * 60)
        );
        const seconds = Math.floor((difference % (1000 * 60)) / 1000);

        setDay(days);
        setHour(hours);
        setMinute(minutes);
        setSecond(seconds);

        setTimeLeft(`${days} ${hours} ${minutes} ${seconds} `);
      } else {
        setTimeLeft(" !");
      }
    };

    calculateTimeLeft();

    const timer = setInterval(calculateTimeLeft, 1000);

    return () => clearInterval(timer);
  }, []);

  return (
    <div className="countInner">
      <div className="countList">
        <div className="numWrap">
          <div className="countDay">
            <span className="num">
              <span className="day1">{day}</span>
            </span>

            <span className="txt">DAYS</span>
          </div>
          <div className="wr">
            <p>
              <span className="hour1">{hour}</span>
            </p>

            <span className="txt">HOURS</span>
          </div>
          <p className="col">
            <span>:</span>
          </p>
          <div className="wr">
            <p>
              <span className="minutes1">{minute}</span>
            </p>

            <span className="txt">MINUTE</span>
          </div>
          <p className="col">:</p>
          <div className="wr">
            <p>
              <span className="seconds1">{second}</span>
            </p>

            <span className="txt">SECONDS</span>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Timer;
