import React, { useEffect } from "react";

const addrX = 127.159514;
const addrY = 35.8485111;

const KakaoMap = () => {
  useEffect(() => {
    const script = document.createElement("script");
    script.src = `https://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=${process.env.REACT_APP_KAKAO_API_KEY}&libraries=services`;
    script.async = true;
    document.head.appendChild(script);

    script.onload = () => {
      window.kakao.maps.load(initMap);
    };

    return () => {
      const kakaoMapScript = document.querySelector(
        '[src="https://dapi.kakao.com/v2/maps/sdk.js?autoload=false&libraries=services"]'
      );
      kakaoMapScript && kakaoMapScript.remove();
      const kakaoServiceScript = document.querySelector(
        '[src="https://dapi.kakao.com/v2/maps/sdk.js?autoload=false&libraries=services"]'
      );
      kakaoServiceScript && kakaoServiceScript.remove();
    };
  }, []);

  const initMap = () => {
    if (!addrX && !addrY) {
      alert("aaa");
    }

    const center = new window.kakao.maps.LatLng(addrY, addrX);
    const options = {
      center,
      level: 3,
    };
    const map = new window.kakao.maps.Map(
      document.getElementById("map"),
      options
    );
    const markerPosition = new window.kakao.maps.LatLng(addrY, addrX);
    const marker = new window.kakao.maps.Marker({
      position: markerPosition,
    });
    marker.setMap(map);
  };

  return (
    <div>
      <div id="map" style={{ width: "100%", height: "165px" }}></div>
    </div>
  );
};

export default KakaoMap;
