import React, { useRef, useEffect } from "react";

const AudioPlayer = () => {
  const audioRef = useRef(null);

  useEffect(() => {
    const audio = audioRef.current;
    audio.play();
    audio.loop = true;

    console.log(audio);
  }, []);

  return (
    <audio ref={audioRef} autoPlay>
      {/* <source src={require("../assets/audio/bgm.m4a")} /> */}
      <source src="../assets/audio/bgm.m4a" type="audio/mp4" />
    </audio>
  );
};

export default AudioPlayer;
